.cow {
  background-color: $background-second;
  padding-top: 125px;
  padding-bottom: 139px;
  background-image: url('../images/cow-bg.png');
  background-repeat: no-repeat;
  background-position-y: 776px;
  background-position-x: center;
  background-size: 100% auto;
  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url('../images/cow-bg@2x.png');
  }

  @media (min-width: 768px) and (max-width: 1199px) {
    padding-top: 130px;
    background-image: none;
  }
  @media (min-width: 1199px) {
    background-position-y: 342px;
  }

  &-subtitle {
    font-family: 'Titan One', sans-serif;
    font-weight: 400;
    font-size: 18px;
    letter-spacing: 4%;
    text-align: center;
    text-transform: uppercase;
    color: $subtitles;
    margin-bottom: 15px;
    @media (min-width: 768px) {
      font-size: 26px;
    }
    @media (min-width: 1199px) {
      font-size: 30px;
    }
  }
  &-title {
    font-family: 'Titan One', sans-serif;
    font-weight: 400;
    font-size: 34px;
    letter-spacing: 6%;
    text-align: center;
    text-transform: uppercase;
    color: $axent;
    margin-bottom: 26px;
    @media (min-width: 768px) {
      font-size: 48px;
      margin-bottom: 50px;
    }
    @media (min-width: 1199px) {
      font-size: 58px;
      margin-bottom: 55px;
    }
  }
  &-wrap {
    display: flex;
    flex-direction: column;
    @media (min-width: 768px) {
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 66px;
    }
    @media (min-width: 1199px) {
      margin-bottom: 39px;
    }
  }
  &-cow {
    width: 280px;
    margin-bottom: 40px;
    @media (min-width: 768px) {
      width: 340px;
      margin-bottom: 0;
    }
    @media (min-width: 1199px) {
      width: 516px;
    }
  }
  &-info {
    width: 220px;
    margin: 0 auto;
    @media (min-width: 768px) {
      width: 340px;
      margin: 0;
      margin-top: 50px;
    }
    @media (min-width: 1199px) {
      width: 463px;
    }
  }
  &-text {
    font-family: 'DM Sans', sans-serif;
    font-weight: 500;
    font-size: 12px;
    line-height: 162%;
    color: $text-second;
    margin-bottom: 17px;
    @media (min-width: 768px) {
      font-size: 14px;
      line-height: 176%;
    }
    @media (min-width: 1199px) {
      margin-bottom: 35px;
      line-height: 194%;
    }
    &:first-child {
      margin-bottom: 30px;
      @media (min-width: 768px) {
        margin-bottom: 35px;
      }
      @media (min-width: 1199px) {
        margin-bottom: 35px;
      }
    }
  }
  &-bold {
    color: $text-first;
    @media (min-width: 1199px) {
      font-size: 16px;
      line-height: 176%;
    }
  }
  &-button {
    display: flex;
    padding: 12px 56px 12px 37px;
    font-family: 'DM Sans' sans-serif;
    font-weight: 700;
    font-size: 14px;
    line-height: 100%;
    letter-spacing: 0%;
    background-color: white;
    border: none;
    border-radius: 22px;
    cursor: pointer;
    background-image: url(../images/button-arrow.svg);
    background-repeat: no-repeat;
    background-position-x: 134px;
    background-position-y: center;
    margin-bottom: 39px;
    color: $axent;
    transition: background-color 300ms ease;
    &:hover,
    &:focus {
      background-color: $main-hover;
    }
    @media (min-width: 768px) {
      margin-top: 62px;
    }
    @media (min-width: 1199px) {
      margin-top: 45px;
      font-size: 16px;
    }
  }
  &-list {
    display: flex;
    flex-direction: column;
    @media (min-width: 768px) {
      flex-direction: row;
    }
  }
  &-item {
    margin-bottom: 38px;
    @media (min-width: 768px) {
      width: 180px;
      margin-right: 60px;
      margin-bottom: 0;
    }
    @media (min-width: 1199px) {
      width: 330px;
      margin-right: 20px;
    }
    &:last-child {
      @media (min-width: 768px) {
        margin-right: 0;
      }
    }
  }
  &-icon {
    height: 50px;
    margin-bottom: 16px;
    @media (min-width: 768px) {
      margin-bottom: 36px;
    }
    @media (min-width: 1199px) {
      margin-bottom: 40px;
    }
  }
  &-number {
    font-family: 'Titan One', sans-serif;
    font-weight: 400;
    font-size: 36px;
    letter-spacing: 4%;
    text-transform: uppercase;
    margin-bottom: 15px;
    color: $axent;
    @media (min-width: 768px) {
      font-size: 48px;
    }
    @media (min-width: 1199px) {
      font-size: 58px;
    }
  }
  &-fact {
    font-family: 'DM Sans', sans-serif;
    font-weight: 700;
    font-size: 12px;
    line-height: 170%;
    @media (min-width: 768px) {
      font-size: 14px;
      line-height: 174%;
    }
    @media (min-width: 1199px) {
      font-size: 16px;
      line-height: 176%;
    }
  }
}
